body {
  background: #80cbc4;
}
.ui.top.attached.menu {
  background: #26a69a;
  border: 1px solid #00796b;
}
.ui.top.attached.menu .menu.right {
  background: #26a69a;
  border-width: 0 1px 0 0;
  box-shadow: none;
  border-radius: 0;
}
.ui.top.attached.menu a {
  color: #fff;
}
.fade-transition {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.fade-enter,
.fade-leave {
  opacity: 0;
}


